body {
	// padding-top: 166px
	// +media-breakpoint-down(lg)
 }	// 	padding-top: 60px
header {
	background: $white;
	box-shadow: 0 10px 20px -10px color-rgba($black,0.1);
	@include pos(rel,null);
	z-index: 1000;
	@include media-breakpoint-down(sm) {
		height: 94px; }
	.container {
		@include media-breakpoint-down(lg) {
			max-width: 100%;
			width: 100%; } }
	.headertop {
		background: darken($white,7%); }
	.colright {
		float: right;
		@include media-breakpoint-down(lg) {
			@include flexbox;
			align-items: center;
			align-content: center; } }
	.hotline {
		float: left;
		a {
			display: block;
			@include fs(14rem);
			line-height: 34px;
			font-weight: 600;
			color: darken($white,60%);
			&:hover {
				color: $brown; } } }
	.check-tool {
		float: left;
		margin-left: 25px;
		+ .check-tool {
			margin-left: 25px; }
		.dropdown {
			.dropdown-toggle {
				display: block;
				@include fs(13rem);
				line-height: 34px;
				color: darken($white,60%);
				&:hover {
					color: $brown; }
				&:after {
					display: none; }
				.fa {
					@include fs(12rem);
					color: $brown;
					margin-right: 5px; } }
			.dropdown-menu {
				padding: 10px;
				.form-group {
					margin-bottom: 10px; }
				label {
					@include fs(13rem); }
				input {
					@include fs(13rem);
					line-height: 30px;
					padding: 0px 10px;
					@include bor-rad(3px);
					border: 1px solid darken($white,10%);
					font-weight: 500; }
				.frm-btn {
					input[type="submit"] {
						@include fs(13rem);
						line-height: 24px;
						padding: 0px 15px;
						@include bor-rad(3px);
						background: $lyellow;
						border: none; } } } } }
	.login {
		float: left;
		margin-left: 25px;
		@include media-breakpoint-down(lg) {
			float: none; }
		ul {
			&:before {
				content: '\f007';
				@include fs(12rem);
				color: $brown;
				display: inline-block;
				vertical-align: middle;
				width: 12px;
				height: 12px;
				line-height: 12px;
				text-align: center;
				@include bor-rad(50px);
				overflow: hidden;
				border: 1px solid $brown; }
			li {
				display: inline-block;
				+ li {
					margin-left: 5px;
					@include pos(rel,null);
					&:before {
						content: '';
						display: block;
						width: 1px;
						height: 10px;
						background: darken($white,30%);
						@include pos(abs, 12px null null -5px); } }
				a {
					display: block;
					color: darken($white,60%);
					line-height: 34px;
					@include fs(13rem);
					&:hover {
						color: $brown; } } } } }
	.headermain {
		background: $white; }
	.sitelogo {
		@include flexbox;
		height: 80px;
		align-items: center;
		float: left;
		@include media-breakpoint-down(lg) {
			height: 60px; }
		a {
			display: block; }
		img {
			// height: 70px
			@include media-breakpoint-down(lg) {
				height: 50px; } } }

	.search {
		float: right;
		// margin-left: 180px
		height: 80px;
		@include flexbox;
		align-items: center;
		@include media-breakpoint-down(lg) {
			height: 60px; }
		.searchwrap {
			width: 100%; }
		.searchbox {
			@include flexbox;
			width: 550px;
			@include bor-rad(50px);
			//overflow: hidden
			border: 1px solid darken($white,20%);
			padding: 1px;
			@include pos(rel,null);
			@include media-breakpoint-down(sm) {
				width: 160px; }
			.search-advance {
				width: 140px;
				border-right: 1px solid darken($white,20%);
				select {
					width: calc(100% - 15px);
					border: none;
					background: none;
					@include fs(13rem);
					text-transform: uppercase;
					font-weight: 700;
					color: darken($white,60%);
					padding: 6px 0 6px 20px;
					option {
						font-weight: 700; } } }
			input {
				background: $white;
				width: calc(100% - 30px);
				padding: 0 20px;
				margin: 0;
				line-height: 30px;
				border: none;
				@include fs(13rem);
				@include bor-rad(30px); }
			.searchbutton {
				width: 30px;
				height: 30px;
				line-height: 30px;
				color: $white;
				// +fs(16rem)
				@include bor-rad(50px);
				background: linear-gradient(135deg, $lyellow, $brown);
				border: none; } } }
	.login-dropdown {
		float: right;
		@include flexbox;
		justify-content: center;
		align-items: center;
		align-content: center;
		height: 60px;
		margin-left: 15px;
		.dropdown-toggle {
			color: $white;
			@include fs(20rem);
			&:after {
				display: none; } }
		.dropdown-menu {
			@include media-breakpoint-down(lg) {
				min-width: 120px; } } }
	.headermenu {
		background: $white; }
	.btn-showmenu {
		@include flexbox;
		float: left;
		width: 40px;
		height: 40px;
		margin-top: 10px;
		@include bor-rad(3px);
		@include fs(24rem);
		align-items: center;
		justify-content: center;
		margin-right: 15px;
		color: $white;
		background: $lyellow; }
	.cart {
		float: right;
		margin-left: 25px;
		@include media-breakpoint-down(lg);
		.cart-toggle {
			color: darken($white,60%);
			@include fs(11rem);
			line-height: 34px;
			a {
				color: darken($white, 60%); }
			.fa {
				@include fs(12rem);
				color: $brown; }
			.cart-amount {
				display: inline;
				line-height: 1;
				&:before {
					content: '('; }
				&:after {
					content: ')'; } } } }
	.btn-showmenu {}
	.overlay {
		display: none;
		@include pos(fix ,0 null 0 0);
		right: 0;
		z-index: 800;
		background: $black;
		opacity: 0.7; }
	.menu {
		@include flexbox;
		flex-wrap: wrap;
		background: $lyellow;
		@include media-breakpoint-down(lg) {
			display: block;
			@include pos(fix, 0 null 0 0);
			z-index: 1000;
			width: 280px;
			background: $white;
			overflow: auto;
			@include transition(all,0.5s);
			@include transform(translateX(-100%));
			&.open {
				@include transform(translateX(0%)); } }
		.btn-closemenu {
			display: none; } }
	.menu-left {
		width: 255px;
		@include pos(rel,null);
		@include media-breakpoint-down(lg) {
			width: 100%; }
		.btn-showleft {
			height: 46px;
			background: $dyellow;
			color: $white;
			@include fs(16rem);
			font-weight: 500;
			text-transform: uppercase;
			line-height: 46px;
			padding: 0px 20px;
			@include media-breakpoint-down(lg);
			.fa {
				@include fs(16rem);
				margin-right: 10px; } }
		&:hover {
			@include media-breakpoint-up(xl) {
				.menu-wrap {
					visibility: visible;
					opacity: 1;
					box-shadow: 0px 5px 20px color-rgba($black,0.1); } } } } }

.homepage {
	header {
		@include media-breakpoint-up(xl) {
			.menu-left {
				.Module, .ModuleContent {
					height: 100%; }
				&:hover {
					.menu-wrap {
						visibility: hidden;
						opacity: 0; } } }
			&.minimal {
				.menu-left {
					&:hover {
						.menu-wrap {
							visibility: visible;
							opacity: 1; } } } } } } }

.menu-left {
	.Module, .ModuleContent {
		height: 100%; }
	.menu-wrap {
		@include pos(abs, 100% 0 null 0);
		z-index: 1000;
		visibility: hidden;
		opacity: 0;
		// padding: 0px 20px
		background: $white;
		@include transition(all,0.5s);
		@include media-breakpoint-down(lg) {
			@include pos(static,unset);
			visibility: visible;
			opacity: 1;
			padding: 0; } }
	ul.menulink {
		@include media-breakpoint-down(lg) {
			padding: 0px 20px; }
		> li {
			padding: 0px 20px;
			&:last-child {
				> a {
					border-bottom: none; } }
			> a {
				display: block;
				@include fs(14rem);
				line-height: 20px;
				color: darken($white,60%);
				padding: 10px 0;
				text-transform: capitalize;
				border-bottom: 1px solid darken($white,5%);
				&:before {
					content: '\f105';
					margin-right: 10px; } }
			&.has-mega {
				.mega {
					@include pos(abs, 0 null 0 100%);
					background: $white;
					padding: 20px;
					width: calc(1180px - 255px - 100px);
					display: none;
					border-left: 1px solid $lyellow;
					box-shadow: 0px 5px 20px color-rgba($black,0.1);
					@include media-breakpoint-down(lg) {
						display: none !important; }
					.mega-sub {
						margin-bottom: 20px;
						.sub-title {
							display: block;
							@include fs(14rem);
							line-height: 1.8;
							color: #cc9909;
							font-weight: 500; }
						ul.sub-link {
							> li {
								> a {
									@include fs(13rem);
									line-height: 1.8;
									color: darken($white,60%);
									&:hover {
										color: $lyellow; } } } } }
					.mega-img {
						display: block;
						img {
							width: 100%; } } } }
			&:hover {
				background: $lyellow;
				> a {
					color: $white;
					font-weight: 700; }
				@include media-breakpoint-up(xl) {
					.mega {
						display: block; } } } } } }

.menu-main {
	width: calc(100% - 255px);
	@include flexbox;
	@include media-breakpoint-down(lg) {
		width: 100%;
		flex-wrap: wrap;
		background: darken($white,3%); }
	.menu-wrap {
		// padding: 0px 15px
		width: calc(100% - 100px);
		@include media-breakpoint-down(lg) {
			width: 100%; }
		ul.menulink {
			@include flexbox;
			// flex-wrap: wrap
			width: 100%;
			@include media-breakpoint-down(lg) {
				flex-wrap: wrap; }
			li {
				flex: auto;
				@include media-breakpoint-down(lg) {
					flex: none;
					width: 100%;
					border-bottom: 1px solid darken($white,10%); }
				a {
					display: block;
					@include fs(14rem);
					font-weight: 400;
					line-height: 20px;
					padding: 13px 0px;
					text-align: center;
					text-transform: uppercase;
					color: $white;
					@include media-breakpoint-down(lg) {
						color: $main;
						text-align: left;
						padding: 10px 20px;
						&:before {
							content: '\f105';
							margin-right: 5px; } } }
				&:hover {
					a {
						background: color-rgba($white,0.2); } } } } }
	.linda-img {
		width: 100px;
		background: linear-gradient(#a96c05, #e0a027);
		@include flexbox;
		justify-content: center;
		align-items: center;
		align-content: center;
		@include media-breakpoint-down(lg) {
			width: 100%;
			padding: 10px 0; } } }
