.page-content {
	padding: 40px 0;
	margin-bottom: 30px; }
.home-title {
	@include fs(24rem);
	font-weight: 600;
	color: $black;
	text-transform: uppercase;
	margin-bottom: 30px;
	&:after {
		content: '';
		display: block;
		width: 50px;
		height: 1px;
		background: $dyellow;
		margin: 10px auto 0; } }
.top-nav {
	padding: 30px 0;
	.container {
		@include media-breakpoint-down(md) {
			max-width: 100%;
			padding: 0; } }
	ul {
		@include flexbox;
		justify-content: center;
		@include bor-rad(50px);
		background: $white;
		@include media-breakpoint-down(md) {
			display: block;
			white-space: nowrap;
			overflow: auto;
			padding: 0px 15px; }
		li {
			@include media-breakpoint-down(md) {
				display: inline-block; }
			+ li {
				margin-left: 40px; }
			&:last-child {
				@include media-breakpoint-down(md) {
					border-right: none; } }
			a {
				display: block;
				@include fs(14rem);
				padding: 0;
				margin: 0;
				// +font-title
				// font-weight: 700
				text-transform: uppercase;
				color: $dyellow;
				border-bottom: 1px solid transparent;
				@include media-breakpoint-down(lg) {
					@include fs(14rem); }
				@include media-breakpoint-down(md) {
					@include fs(13rem); } }
			&:hover {
				a {
					color: $pink; } }
			&.active {
				a {
					color: $pink;
					border-color: $pink; } } } } }


.about-section {
	padding: 120px 0 0;
	@include pos(rel,null);
	&:before {
		content: '';
		display: block;
		width: 32px;
		height: 105px;
		background: url(../img/sectionbg.png) center center no-repeat;
		background-size: cover;
		@include pos(abs, 0 null null 50%);
		@include transform(translateX(-50%)); }
	.about-content {
		img {
			max-width: unset; } }
