@import url('https://fonts.googleapis.com/css?family=Dancing+Script:400,700|Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i&subset=cyrillic,cyrillic-ext,greek,greek-ext,latin-ext,vietnamese');

@import "color";

// MATERIAL VARIABLE
@mixin flexbox {
	display: -webkit-box;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flexbox;
	display: flex; }
@mixin clearfix {
	content: '';
	display: table;
	width: 100%;
	clear: both; }

@mixin mainbutton {
	display: block;
	background: linear-gradient(to right, $dyellow, $lyellow, $dyellow);
	color: $white;
	@include fs(13rem);
	font-weight: 500;
	text-transform: uppercase;
	@include bor-rad(500px);
	line-height: 40px;
	padding: 0px 50px;
	&:hover {
		filter: brightness(110%);
		box-shadow: 0px 10px 20px -10px $dyellow; } }

@mixin fs($size) {
	font-size: ($size / 16); }
@mixin font-title() {
	font-family: 'Arima Madurai', cursive; }

body {
	margin: 0;
	padding: 0;
	@include fs(15rem);
	font-family: 'Roboto', sans-serif;
	color: $main;
	background: $white;
	line-height: 1.5;
	@include media-breakpoint-down(sm) {
		@include fs(14rem); } }
html {
	overflow: auto; }
.fullpage {
	overflow: hidden;
	max-width: 1600px;
	margin: 0 auto; }
a {
	color: $blue;
	@include transition(all,0.3s);
	&:hover {
		color: $cyan; } }
a, a:hover {
	&:focus, &:active, &:hover {
		outline: none;
		text-decoration: none; } }
input,button, select, textarea {
	@include transition(all,0.3s);
	&:hover, &:active, &:focus {
		outline: none; } }
button {
	border: none;
	background: none;
	margin: 0;
	padding: 0;
	@include transition(all,0.3s); }
img, iframe {
	vertical-align: middle; }

.container {
	@include pos(static,null);
	@include media-breakpoint-up(xl) {
		max-width: 1210px;
		width: 100%; }
	@include media-breakpoint-down(lg) {
		width: 100%;
		max-width: 100%; } }

.content {
	line-height: 1.5;
	// +fs(14rem)
	@include media-breakpoint-down(md) {
		@include fs(14rem);
		line-height: 1.8; }
	ul, ol, p, h1, h2, h3, h4, h5, h6, img, figure {
		margin-bottom: 10px;
		line-height: 1.5;
		@include media-breakpoint-down(xs) {
			margin-bottom: 10px; }
		&:last-child {
			margin-bottom: 0; } }
	strong {
		font-weight: 500; }
	figure {
		margin: 0 auto 10px;
		text-align: center;
		@include media-breakpoint-down(xs) {
			margin-bottom: 10px; }
		img {
			margin-bottom: 5px; }
		figcaption {
			// background: darken($white,5%)
			padding: 5px 0;
			text-align: center;
			@include fs(13rem);
			font-style: italic;
			color: darken($white,60%); } }
	ul {
		list-style-type: disc;
		padding-left: 30px;
		li {} } }

.row {
	margin: 0px -10px;
	&.no-gutters {
		margin: 0; } }
[class^="col-"] {
	padding: 0px 10px;
	@include transition(all,0.3s);
	@include pos(static,null); }
.col-12 {
	width: 100%; }
.nopadding {
	padding: 0; }
* {
	&:hover, &:active, &:focus {
		outline: none; }
	&:before, &:after {
		font-family: 'fontawesome'; } }
.lnr {
	font-family: 'linearicons-free';
	&:before, &:after {
		font-family: 'linearicons-free'; } }
