		.point {
			// font-weight: 500
			color: $gray; }
		.link-detail {
			color: $gray;
			font-weight: normal;
			margin-left: 10px;
			&:hover {
				color: $lyellow;
 } }				// text-decoration: underline
		th {
			border: none;
