.shipping-progress {
	@include flexbox;
	margin-bottom: 30px;
	.step {
		width: 20%;
		text-align: center;
		padding: 0px 20px;
		@include pos(rel,null);
		.icon {
			width: 60px;
			height: 60px;
			@include bor-rad(50px);
			line-height: 54px;
			margin: 0px auto 30px;
			@include fs(24rem);
			color: darken($white,40%);
			border: 2px solid darken($white,20%);
			&:after {
				content: '';
				display: block;
				width: 100%;
				height: 4px;
				background: darken($white,20%);
				margin-top: 20px;
				@include pos(abs, null 0 null 0);
 } }				// +transform(translateX(-50%))
		.status {
			font-weight: 500;
			@include fs(13rem);
			color: $gray;
			line-height: 1.3; }
		&:first-child {
			.icon {
				// &:after
 } }				/// display: none
		&.active {
			.icon {
				color: $white;
				background: $gray;
				border-color: $gray;
				&:after {
					background: $gray; } } } } }
.account-info {
	@include pos(rel,null);
	margin-bottom: 15px;
	// +media-breakpoint-down(md)
	// 	margin-bottom: 20px
	.info-detail {
		border: 1px solid darken($white,10%);
