 } }		// text-transform: uppercase

.shipping-step {
	@include flexbox;
	margin-bottom: 20px;
	.step {
		width: 33.33%;
		@include pos(rel,null);
		@include flexbox;
		flex-wrap: wrap;
		justify-content: center;
		&:before {
			content: '';
			display: block;
			width: 100%;
			height: 3px;
			background: darken($white,20%);
			@include pos(abs, 40px 50% null null);
			@include transform(translateY(-1px)); }
		&:first-child {
			&:before {
				display: none; } }
		.icon {
			@include flexbox;
			width: 80px;
			height: 80px;
			@include bor-rad(50%);
			justify-content: center;
			align-items: center;
			border: 3px solid darken($white,20%);
			@include pos(rel,null);
			z-index: 1;
			background: $white;
			img {
				max-width: 60%;
				max-height: 60%; } }
		.name {
			width: 100%;
			text-align: center;
			padding: 10px 0;
			font-weight: 500;
			color: darken($white,60%); }
		&.active {
			&:before {
				background: $lyellow; }
			.icon {
				border-color: $lyellow; }
			.name {
				color: $lyellow; } } } }

.ui-widget-overlay {
	position: fixed;
	z-index: 1000;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: $black;
	opacity: 0.5; }
.ui-dialog-titlebar-close {
	@include pos(abs, -15px -15px null null);
	color: $white;
	width: 40px;
	z-index: 1005;
	height: 40px;
	line-height: 10px;
	text-align: center;
	background: $red;
	@include bor-rad(50px);
	&:after {
		content: '\f00d';
		font-family: "FontAwesome";
		@include fs(20rem); } }
.ui-dialog {
	z-index: 1001;
	@include pos(fix,null);
	right: 0 !important;
	left: 0 !important;
	top: 15px !important;
	width: 100% !important;
	max-width: 290px;
	margin: 0 auto;
	// padding: 20px
	background: $white;
	@include media-breakpoint-up(sm) {
		max-width: 390px; }
	@include media-breakpoint-up(md) {
		max-width: 700px;
		top: 20px !important; }
	@include media-breakpoint-up(lg) {
		max-width: 800px;
		top: 40px !important; }
	.ui-dialog-title {
		display: none; }
	.productAddedToCartWindow {
		@include border-radius(5px);
		@include pos(abs, 0 null 0 0);
		right: 0;
		z-index: 1000; }
	.modal-content {
		border: none; }
	.modal-dialog {
		margin: 0 auto; } }
.ui-dialog-wishlist {
	padding: 30px 20px;
	text-align: center;
	.ui-dialog-titlebar-close {
		top: -15px; }
	.ui-dialog-content {
		@include fs(16rem);
		font-weight: 600;
		line-height: 1.4;
		min-height: auto!important;
		text-align: center;
		display: block;
		margin-bottom: 20px; }
	.ui-dialog-buttonpane {
		text-align: center;
		button {
			color: $white;
			display: inline-block;
			font-size: 1rem;
			background-color: $red;
			line-height: 35px;
			padding: 0px 15px;
			@include transition(all 0.4s);
			&:hover, &:focus {
				background-color: darken($red, 8%); }
			& + button {
				margin-left: 10px; } } } }
